<template>
    <div class="page-article" v-if = "model">
        <div class="d-flex py-3 px-3 border-bottom text-news-blue ">
                <div class="iconfont icon-back"></div>
                <strong class="flex-1 text-ellipsis">
                    {{model.title}}
                </strong>
                <div class="text-grey fs-sm">
                    {{ model.createdAt | date}}
                </div>
        </div>
        <div v-html="model.body" class="px-5 articleBody fs-lg"></div>
        <div class="px-3 py-2 border-top relateNews">
            <div class="px-3 ai-center">
                <i class="iconfont icon-cc-menu-circle"></i>
                <strong class="text-blue fs-lg ml-1 text-news-blue">News and Information</strong>
            </div>
            <router-link tag="div" class="text-ellipsis px-3 my-2 routerBox" :to="`/articles/${item._id}`"  v-for="item in model.relateArticle" :key="item._id">
                {{ item.title }}
            </router-link>
        </div>
    </div>
</template>
<script>
import dayjs from 'dayjs'
    export default{
        
        props:{
            id:{require:true}
        },
        filters:{
            date(val){
            return dayjs(val).format('MM/DD')
            }
        },
        data(){
            return {
                model:null
            }
        },
        watch:{
            id(){
               this.fetch()
            }
        },

        methods:{
            async fetch(){
                const res = await this.$http.get(`articles/${this.id}`)
                this.model = res.data
            }
        },
        created(){
            this.fetch()
        }
    }
</script>
<style lang="scss">
.page-article{
    .articleBody{
        img{
            max-width: 100%;
            height: auto;
        }
        iframe{
            max-width: 100%;
            height: auto;
        }
    }
    .relateNews{
        .routerBox{
            width:70%;
            display: block;
        }
    }
}
</style>